.body {
    font-family: 'Comic Sans Ms';
    margin: 0px;
}

.header {
    &__title{
        text-align: center;
        font-size: 50px;
    }
}

.statusGame {
  display: flex;
  justify-content: space-between;
  padding: 0 40px;
  font-size: 30px;
  padding-bottom: 80px;

  &__start{
      background-color: #19c575;
      border-color: #19c575;
      color: #fff;
      width: 80px;
  }

}
.playArea {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
  
  .ground {
    display: inline-flex;
    flex: 1 1 25%;
    justify-content: center;
    align-items: center;
    padding: 10px;
  }
  
  .wrapper {
    position: relative;
    padding-bottom: 100px;
    overflow: hidden;
  }
  
  .dirt {
    height: 70px;
    background-position: center;
  }
